.flybear-app {
  text-align: center;
  background-color: #000;
  color: white;

  width: 100%;
  height: 100%;

  position: relative;
}

.flybear-title {
  font-size: 19vw;
  text-align: left;
  font-weight: bold;
  line-height: 17vw;
  margin-top: 3vw;

  color: black;
  text-shadow: #000a .5vmin .5vmin;
}

.flybear-title-wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;

  background: linear-gradient( 125deg, #00ff00, #00ffff)
}

.flybear-section {
  height: 100%;
  background: var(--bkg);

  position: relative;
}

.flybear-header {
  font-size: 10vmin;
  font-weight: 700;

  position: absolute;
  left: 5vmin;
  top: 5vmin;

  background: #00f3f3ee;
}
